<template>
    <div class="more-api">
        <div class="more-api-box">
			<!--banner -->
			<div class="khhd" :loading="leftLoading">
				<div class="gfdd">
					<div class="iuhg">
						<img src="../../assets/leftAside/888_.png"/>
						<p></p>
					</div>
					<span>({{total}})</span>
				</div>
				<div v-if="showLeft">
					<el-menu
					:default-openeds="[shows.toString()]" 
					:default-active="shows + '-' + selectId"
					:unique-opened="true"
					@select="handleSelect"
					@open="handleOpen"
					@close="handleClose">
					  <el-submenu v-for="item in dimList" :index="item.dimId.toString()" :key="item.dimId">
						<template slot="title"><img class="sokj" :src="item.dimDefPicture"/>{{item.dimName}} <span>({{item.intfNum}})</span></template>
						<el-menu-item-group>
						  <el-menu-item v-for="demo in item.intfList" :index="item.dimId+ '-' + demo.id" :key="demo.id">{{demo.name}}</el-menu-item>
						</el-menu-item-group>
					  </el-submenu>
					</el-menu>
				</div>
			</div>
			<div class="router_box">
			    <div class="ewdss">
					<div class="rghss">
						<div class="idwwwd">
							<div style="display: flex;align-items: center;">
								<el-select v-model="secretVal"  @change="scretChange" placeholder="">
									<el-option
									  v-for="item in options02"
									  :key="item.value"
									  :label="item.miyao"
									  :value="item.value">
									</el-option>
								</el-select>
								<el-tooltip class="item" effect="dark" content="" placement="top-start">
									<div class="thssw">?</div>
								</el-tooltip>
							</div>
							<p v-if="secretVal">{{scretLabel}}</p>
							<!-- <p v-if="secretVal">2000</p> -->
						</div>
						<div class="edff">
							<span></span>
							<p>{{detailObj.name}}</p>
						</div>
						<div class="edff">
							<span>ID</span>
							<p>{{detailObj.showId}}</p>
						</div>
						<div class="edff">
							<span></span>
							<p>{{detailObj.reqMethod}}</p>
						</div>
						<div class="edff">
							<span></span>
							<p>{{detailObj.respFormat}}</p>
						</div>
						<div class="edff">
							<span class="ojhb">Query</span>
							<p>{{detailObj.reqParam}}</p>
						</div>
						<div class="edff yhjw" v-for="(item,index) in inputList">
							<span>{{item.name}}</span>
							<el-input class="yhsdd" v-model="editForm[index]" :placeholder="item.desc" auto-complete="off"></el-input>
						</div>
						<!-- <div class="edff">
							<span>keyword</span>
							<el-input v-model="editForm.keyword" placeholder="id//" auto-complete="off"></el-input>
						</div> -->
						<!-- <div class="edff">
							<span>pageNum</span>
							<el-input v-model="editForm.pageNum" placeholder="" auto-complete="off"></el-input>
						</div>
						<div class="edff">
							<span>pageSize</span>
							<el-input v-model="editForm.pageSize" placeholder="1020" auto-complete="off"></el-input>
						</div> -->
						<div class="oiud" @click="editSubmit()"></div>
					</div>
					<div class="oiuyd">
						<div class="edsff"></div>
						<div class="efgbs">
							<p>{{detailObj.reqMethod}}</p>
							<p><br/>{{detailObj.domainUrl}}</p>
							<p><br/>{{getQuery}}</p>
							<p>secret<br/>{{scretLabel}}</p>
						</div>
						<div class="dfghn">
							<p></p>
							<span @click="copy"></span>
						</div>
						<div class="cdssa">
							<pre>{{ respExample?JSON.stringify(respExample, null, 4):''  }}</pre>
						</div>
					</div>
				</div>
			</div>
		</div>
    </div>
</template>
<script>
import copy from 'copy-to-clipboard'
export default {
	name: 'apiDetail',
    data(){
        return{
            shows:0,
            selectId: 0,
			dimList: [],
			// dimListDetail:{},
			showLeft: false,
			leftLoading: false,
			total: 0,
			detailObj: {},
			options01: [],
			rightIndex: 1,
			activeName: 'first',
			  editForm: {},
			  options02: [],
			  scretLabel: '',
			  orderId: '',
			// editFormRules: {
			// 	// informChannel: [
			// 	// 	{ required: true, message: '', trigger: 'blur' }
			// 	// ],
			// 	// planName: [
			// 	// 	{ required: true, message: '', trigger: 'blur' }
			// 	// ],
			// 	// editForm: [
			// 	// 	{ required: true, message: '', trigger: 'blur' }
			// 	// ]
			// },
			editLoading: false,
			domainUrl: '',
			respExample: '',
			inputList: [],
			secretVal: ''
		}
    },
	computed: {
		getQuery() {
			let str = ''
			for(let i=0;i<this.inputList.length;i++) {
				let key = this.inputList[i].name
				str += `${key}=${this.editForm[i] || null};`
			}
			return str
		}
	},
    methods:{
		copy() {
			let _this = this
			copy(JSON.stringify(_this.respExample))
			this.$message.success('');
		},
		handleClick(tab, event) {
			console.log(tab, event);
		},
		handleOpen(key, keyPath) {
			console.log(key, keyPath);
	    },
	    handleClose(key, keyPath) {
		 console.log(key, keyPath);
	    },
		handleSelect(key, keyPath) {
			let selectId = parseInt(keyPath[1].split('-')[1])
			this.shows = parseInt(key)
			this.selectId = selectId
			this.getDetail(selectId)
			window.scrollTo(0,0);
			this.initInput()
			this.$cookies.set('shows', this.shows, { expires: 1 })
			this.$cookies.set('selectId', selectId, { expires: 1 })
	    },
		scretChange(val) {
			this.options02.forEach(item=>{
				// console.log(item.value == val)
				// console.log(item.value)
				// console.log(val)
				// console.log(item.label)
				if (item.value === val) {
					this.scretLabel = item.label
				}
				this.orderId = val
			})
		},
		editSubmit() {
			let user = this.$cookies.getJSON('user')
			if (user && user.userId) {
				if(!this.secretVal) {
				   this.$message.error('');
				   return
				}
				// if (!this.editForm.keyword) {
				// 	this.$message.error('');
				// 	return
				// }
				// if (!this.editForm.pageNum) {
				// 	this.$message.error('');
				// 	return
				// }
				// if (!this.editForm.pageSize) {
				// 	this.$message.error('');
				// 	return
				// }
				// this.$store.commit('setindex',index)
				// this.indexSpan=this.$store.state.indexspan
				// console.log(document.getElementsByTagName('input'))
				let valid = false
				let count = 0
				for(let key in this.editForm) {
					count++
				}
				if (count<this.inputList.length) {
					valid = true
				} else {
					for(let key in this.editForm) {
						if (!this.editForm[key]) {
							valid = true
						}
					}
				}
				if (valid) {
					this.$message.error('');
					return
				}
				let newObj = {}
				for(let i=0;i<this.inputList.length;i++) {
					let name = this.inputList[i].name
					newObj[name] = this.editForm[i]
				}
				this.$axios.get(`http://${this.domainUrl}`,newObj,{secretkey: this.scretLabel})
				  .then((res) =>{
					  if (res.code==='200') {
						this.respExample = res.data
					  } else {
						this.$message.error(res.message);
					  }
				  })
				  .catch(err=>{
					  this.$message.error(err);
				  })

			} else {
				this.$store.commit('setShowLogin',1)
			}
		},
		getLeft() {
			this.leftLoading = true
			// 8027
			this.$axios.get(`${this.host_url}portal/api/dim/interface/detail`)
			  .then((res) =>{
				  if (res.state===200) {
					
				  } else {
					this.$message.error(res.message);
				  }
				  this.dimList = res.data.item
				  this.afterLeft()
				  this.total = res.data.total
				  // this.resolveDimList(this.dimList)
				  this.showLeft = true
				  this.leftLoading = false
			  })
			  .catch(err=>{
				  this.$message.error(err);
				  this.leftLoading = false
			  })
		},
		getDetail(id) {
			// 8027
			this.$axios.get(`${this.host_url}portal/api/interface/details`,{id: id})
			.then((res) =>{
				this.detailObj = res.data
				this.domainUrl = res.data.domainUrl
				this.inputList = JSON.parse(res.data.reqParamPretty)
			})
			this.selectId = id
		},
		solveArr(arr) {
			if( arr.length === 0) {
				this.options02 = []
				return
			}
			let arro= []
			arr.forEach((item,index)=>{
				let obj = {}
				obj['value'] = item.orderId
				obj['label'] = item.secret
				obj['miyao'] = ''+(index+1)
				arro.push(obj)
			})
			// this.editForm.secret = arro[0].value
			this.scretLabel = arro[0].label
			this.options02 = arro
		},
		initInput() {
			// this.editForm.keyword = ''
			// this.editForm.pageNum = ''
			// this.editForm.pageSize = ''
			this.editForm = {}
			this.respExample = ''
			this.secretVal = ''
		},
		getScrets() {
			let params = {
				  userId: this.$cookies.getJSON('user').userId,
				  billingMode: 1
			  }
			  // 8023
			this.$axios.get(`${this.host_url}order/secret`,params)
			  .then((res) =>{
				  if (res.state===200) {
					
				  } else {
					this.$message.error(res.message);
				  }
				  this.solveArr(res.data)
			  })
			  .catch(err=>{
				  this.$message.error(err);
			  })
		},
		afterLeft() {
			this.shows = this.$cookies.get('shows') || this.$route.query.dimId || this.dimList[0].dimId
			this.selectId = this.$cookies.get('selectId') || this.$route.query.id || this.dimList[0].intfList[0].id
			this.getDetail(this.selectId)
		}
    },
	beforeDestroy() {
		this.$cookies.remove('shows')
		this.$cookies.remove('selectId')
	},
    mounted(){
		this.getLeft()
		if (this.$cookies.getJSON('user').userId) {
			this.getScrets()
		}
		// let selectId = this.$cookies.get('selectId')
		// if (selectId) {
		// 	this.sho
		// }
    }
}
</script>
<style lang="less" scoped>
	.more-api {
		background: #F6F8FE;
		padding-bottom: 55px;
		.more-api-box {
			width: 1200px;
			margin: 0 auto;
			display: flex;
			justify-content: space-between;
			.khhd {
				width: 224px;
				height: 100%;
				background-color: #fff;
				margin-top: 20px;
				.gfdd {
					display: flex;
					align-items: center;
					justify-content: space-between;
					background: #FBFBFB;
					height: 48px;
					padding: 0 38px 0 20px;
					box-sizing: border-box;
					.iuhg {
						display: flex;
						align-items: center;
						img {
							width: 14px;
							font-size: 14px;
							color: #333333;
							padding-right: 8px;
						}
					}
				}
				.el-menu {
					border-right: none;
					.sokj {
						width: 14px;
						padding-right: 8px;
						position: relative;
						top: -3px;
					}
					span {
						position: absolute;
						right: 42px;
						top: 0;
						width: 23px;
						text-align: center;
					}
				}
			}
			.router_box {
				width: 956px;
				margin-top: 20px;
				.ewdss {
					display: flex;
					justify-content: space-between;
					height: 100%;
					.rghss {
						width: 384px;
						background-color: #fff;
						height: 100%;
						padding: 16px;
						box-sizing: border-box;
						.idwwwd {
							background: #F5F5F5;
							border: 1px solid #EEEEEE;
							border-radius: 2px;
							font-size: 14px;
							color: #000000;
							padding: 12px 16px;
							p {
								margin-top: 10px;
							}
						}
						.edff {
							display: flex;
							font-size: 14px;
							margin-top: 20px;
							span {
								display: block;
								// width: 104px;
								// text-align: right;
								color: #666666;
							}
							p {
								word-break: break-all;
								word-wrap: break-word;
								padding-left: 2px;
							}
							.ojhb {
								width: 139px;
								white-space: nowrap;
							}
							.el-input {
								width: 246px;
								margin-left: 4px;
							}
						}
						.yhjw {
							display: flex;
							align-items: center;
						}
						.oiud {
							background: #1C66E5;
							width: 116px;
							height: 36px;
							line-height: 36px;
							text-align: center;
							border-radius: 2px;
							color: #fff;
							margin: 24px auto;
							cursor: pointer;
						}
						.oiud:active {
							opacity: 0.6;
						}
					}
					.oiuyd {
						width: 560px;
						background-color: #fff;
						height: 100%;
						padding: 12px 16px;
						box-sizing: border-box;
						font-size: 14px;
						.efgbs {
							height: 234px;
							background: #F5F5F5;
							border: 1px solid #EEEEEE;
							border-radius: 2px;
							margin-top: 12px;
							padding: 16px;
							box-sizing: border-box;
							word-break: break-all;
							word-wrap: break-word;
							overflow: scroll;
							p {line-height: 28px;}
						}
						.dfghn {
							margin-top: 12px;
							display: flex;
							justify-content: space-between;
							align-items: center;
							p {
								font-weight: 500;
								cursor: pointer;
							}
							span {
								width: 67px;
								height: 28px;
								line-height: 28px;
								text-align: center;
								display: block;
								border: 1px solid #E0E0E0;
								border-radius: 4px;
								cursor: pointer;
							}
							span:active {
								opacity: 0.8;
							}
						}
						.cdssa {
							height: 602px;
							background: #F5F5F5;
							border: 1px solid #EEEEEE;
							border-radius: 2px;
							margin-top: 12px;
							overflow: scroll;
							padding: 20px;
							box-sizing: border-box;
						}
					}
				}
			}

		}
	}
</style>


