<template>
    <div class="dlkj">
		<div class="rhjss" @click="detail=false" v-if="detail">/<span></span></div>
		
        <secret @orderId="getOrderId"></secret>
		<div class="bujss" v-loading="tableLoading">
			<div v-if="!detail">
				<h6 class="joid"></h6>
				<!-- <div class="juidj">
					<el-date-picker
					  v-model="date"
					  type="daterange"
					  range-separator=""
					  start-placeholder=""
					  end-placeholder="">
					</el-date-picker>
					<div @click="getCase" :class="['rtgh',{'rtgh-diabled': !date}]" :disabled="!date"></div>
				</div> -->
			</div>
			<div class="tgns" v-if="detail">
				<el-input
					placeholder=""
					clearable
					v-model="input1">
					<i @click="getCase" slot="suffix" class="el-input__icon el-icon-search"></i>
				</el-input>
			</div>
			<div class="ryyh" v-if="!detail">
				<el-table
				    :data="tableData"
				    style="width: 100%">
				    <el-table-column
				      label=""
				      width="140">
					  <template slot-scope="scope">
						{{ scope.row.invokeMonth }}
					  </template>
				    </el-table-column>
				    <el-table-column
				      label=""
				      width="200">
				      <template slot-scope="scope">
						{{ scope.row.invokeNum }}
				      </template>
				    </el-table-column>
					<el-table-column
					  label=""
					  width="200">
					  <template slot-scope="scope">
							{{ scope.row.paidNum }}
					  </template>
					</el-table-column>
					<el-table-column
					  label=""
					  width="180">
					  <template slot-scope="scope">
						{{ scope.row.actualCost }}
					  </template>
					</el-table-column>
				    <el-table-column label="" width="180">
				      <template slot-scope="scope">
				        <span class="guuus" @click="handleEdit(scope.$index, scope.row)"></span>
				      </template>
				    </el-table-column>
				  </el-table>
			  <div class="wdwds" v-if="tableData.length===0">
			  	<img src="../../../assets/kong/kong_.png"/>
			  	<p></p>
			  </div>
			</div>
			
			<div class="ryyh" v-if="detail">
				<el-table
				    :data="tableData"
				    style="width: 100%">
					<el-table-column
					  label=""
					  width="140">
					  <template slot-scope="scope">
						{{ scope.row.intfName }}
					  </template>
					</el-table-column>
				    <el-table-column
				      label=""
				      width="140">
					  <template slot-scope="scope">
						{{ scope.row.invokeNum }}
					  </template>
				    </el-table-column>
				    <el-table-column
				      label=""
				      width="200">
				      <template slot-scope="scope">
						{{ scope.row.successNum }}
				      </template>
				    </el-table-column>
					<el-table-column
					  label=""
					  width="200">
					  <template slot-scope="scope">
							{{ scope.row.paidNum }}
					  </template>
					</el-table-column>
					<el-table-column
					  label=""
					  width="180">
					  <template slot-scope="scope">
						{{ scope.row.actualCost }}
					  </template>
					</el-table-column>
				  </el-table>
			  <div class="wdwds" v-if="tableData.length===0">
			  	<img src="../../../assets/kong/kong_.png"/>
			  	<p></p>
			  </div>
			  <div v-if="pages>10" class="erfr">
			  	<el-pagination
			  		background	
			  		:current-page="currentPage"
			  	    layout="prev, pager, next"
			  		@current-change="handleCurrentChange"
			  	    :total="pages">
			  	  </el-pagination>
			  </div>
			</div>
			
		</div>
        <router-view></router-view>
    </div>
</template>
<script>
import secret from '../../../components/secret.vue'
export default {
    namme:'case3',
	components:{
	  secret
	},
    data(){
        return{
            // list:[],
            // caseId:'',
			// ifSee: true,
			input1: '',
			tableLoading: false,
			date: '',
			detail: false,
			orderId: '',
			// img1: require('../../../assets/common/ddd.png'),
			// img2: require('../../../assets/common/ddd_.png'),
			// options01: [{
			//   value: 1,
			//   label: '1'
			// }, {
			//   value: 2,
			//   label: '2'
			// }],
			// filters01: '',
			// filters02: '',
			tableData: [],
			pages: 0,
			currentPage: 1,
			rowObj: {}
        }
    },
    methods:{
		getOrderId(val) {
			if(!val) return
			this.orderId = val
			this.getCase()
		},
		getCase:function(page){
		    let params = {
				  orderId: this.orderId,
				  // name: this.input1,
				  // date: this.date,
				  pageNum: page || 1
			  }
			this.tableLoading = true
			// 8023
		    this.$axios.get(`${this.host_url}order/invoke/statistics`,params)
			  .then((res) =>{
				  console.log(res)
				  if (res.state===200) {
					
				  } else {
					this.$message.error(res.message);
				  }
				  try{
				  	this.tableData = res.data
					console.log(this.tableData)
				  }catch(e){
				  	//TODO handle the exception
				  }
				  this.tableLoading = false
			  })
			  .catch(err=>{
				  this.tableLoading = false
				  alert(err);
			  })
		},
		getDetail(row,page) {
			this.tableLoading = true
			// 
			// this.date = ''
			this.input1 = ''
			let _this = this
			let params = {
				  orderId: row.orderId,
				  intfName: row.intfName,
				  month: row.invokeMonth,
				  pageNum: page
			  }
			this.$axios.get(`${this.host_url}order/invoke/statistics/detail`,params)
			  .then((res) =>{
				  console.log(res)
				  if (res.state===200) {
					this.detail = true
				  } else {
					this.$message.error(res.message);
				  }
				  try{
				  	this.tableData = res.data.records
					this.pages = res.data.total
				  }catch(e){
				  	//TODO handle the exception
				  }
				  this.tableLoading = false
			  })
			  .catch(err=>{
				  this.tableLoading = false
				  alert(err);
			  })
		},
		handleCurrentChange(val) {
			this.getDetail(this.rowObj,val)
		},
		handleEdit(index, row) {
			this.getDetail(row,1)
			this.rowObj = row
		  },
		  handleDelete(index, row) {
			console.log(index, row);
		  }
    },
    mounted() {
    	this.$store.commit('setCenterIndex',3)
    }
}
</script>
<style lang="less" scoped>
	.dlkj {
		.rhjss {
			font-size: 14px;
			color: #8F959E;
			margin-bottom: 14px;
			cursor: pointer;
			span {
				color: #1F2329;
			}
		}
		.vhdd {
			.obhh {
				padding: 20px;
				background-color: #fff;
				display: flex;
				align-items: center;
				color: #666666;
				font-size: 14px;
				margin-bottom: 16px;
				p {
					width: 70px;
				}
				.el-select {
					padding-right: 20px;
				}
				span {
					margin-right: 10px;
				}
				img {
					width: 14px;
				}
			}
			
		}
		
		.bujss {
			background-color: #fff;
			margin-top: 16px;
			padding: 16px 20px;
			box-sizing: border-box;
			.tgns {
				width: 203px;
				margin-bottom: 16px;
			}
			.joid {
				font-size: 16px;
				font-weight: 500;
				position: relative;
				margin-bottom: 16px;
				&::after {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					bottom: 2px;
					width: 64px;
					height: 6px;
					background-color: #329DFF;
					border-radius: 4px;
					opacity: 0.5;
				}
			}
			.juidj {
				display: flex;
				align-items: center;
				margin-top: 20px;
				margin-bottom: 16px;
				.el-input {
					width: 203px;
				}
				.rtgh {
					width: 100px;
					height: 40px;
					text-align: center;
					line-height: 40px;
					color: #fff;
					background-color: #1C66E5;
					margin-left: 12px;
					border-radius: 2px;
					cursor: pointer;
					&:active {
						opacity: 0.8;
					}
				}
				.rtgh-diabled {
					background: linear-gradient(0deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05)), #FFFFFF;
					border: 1px solid #E0E0E0;
				}
			}
			
			.ryyh {
				.guuus {
					color: #1C66E5;
					cursor: pointer;
				}
			}
		}
	}
</style>


