</template>
<script>
import secret from '../../components/secret.vue'

export default {
  name: 'charge',
  components:{
    secret
  },
  data(){
    return{
		form: {
			number: '',
			type: 0,
			money: ''
		},
		dialogFormVisible: false,
		checked: false,
		resHtml: '',
		state: 0,
		resultVisible: false,
		giveAmount: undefined,
		chargeState: 'success'
    }
  },
  watch: {
	  'form.number': function(newVal,oldVal) {
		  if (newVal != 0) {
			  this.form.money = ''
			  this.getGiveMoney(newVal)
		  }
	  },
	  'form.money': function(newVal,oldVal) {
		  if (newVal != 0) {
			  this.form.number = 0
			  this.getGiveMoney(newVal)
		  }
		  if (this.form.money>0) {
			  this.form.money = Math.floor(newVal)
		  }
		  if ( this.form.money>100000) {
			  this.form.money = 100000
		  }
	  }
  },
  methods:{
	  getOrderId(val) {
		if(!val) return
		this.orderId = val
	  },
    getGiveMoney (money) {
		if (money>=20000) {
			this.giveAmount = 5000
			return
		} else if(money>=10000) {
			this.giveAmount = 2500
			return
		} else if(money>=5000) {
			this.giveAmount = 1000
			return
		} else if(money>=2000) {
			this.giveAmount = 200
			return
		} else if(money>=500) {
			this.giveAmount = 50
			return
		} else {
			this.giveAmount = 0
		}
	},
	repay () {
		let user = this.$cookies.getJSON('user')
		if (user && user.token) {
			if (!this.form.number && !this.form.money) {
				this.$message.error('');
				return
			}
			if (this.form.money<0) {
				this.$message.error('');
				return
			}
			if (this.form.money>0 && this.form.money<500) {
				this.$message.error('500');
				return
			}
			if (!this.form.type) {
				this.$message.error('');
				return
			}
			if (!this.checked) {
				this.$message.error('');
				return
			}
			// this.dialogFormVisible = true
			this.submitMoney()
		} else {
		  this.$store.commit('setShowLogin',1)
		}
		
	},
	inputMoney() {
		this.form.number = 0
		this.giveAmount = undefined
	},
	submitMoney() {
		let user = this.$cookies.getJSON('user')
		if (!user || !user.userId) {
			this.$store.commit('setShowLogin',1)
			return
		}
		if(this.state===1) {
			this.$message.error('');
			this.jump()
			return
		}
		if(this.state===2) {
			this.$message.error('...');
			return
		}
		if(this.state===3) {
			this.$message.error('');
			return
		}
		
		let charge = 0
		if (this.form.number!=100){
			charge = this.form.number?this.form.number*100:this.form.money*100
		}
		let give = this.giveAmount?this.giveAmount*100:0
		let params = {
			  userId: this.$cookies.getJSON('user').userId,
			  orderId: this.orderId,
			  payType: parseInt(this.form.type),
			  chargeType: 1,
			  amount: charge,
			  giveAmount: give
		  }
		  // 8025
		this.$axios.post(`${this.host_url}pay/amount/charge`,params)
		  .then((res) =>{
			  console.log(res)
			  if (res.state===200) {
				if (res.data === 'success') {
					this.successCall()
				} else if(res.data === 'fail'){
					this.failCall()
				} else {
					this.resHtml = res.data
					const newWindow = window.open('', '_blank');  
					newWindow.document.write(res.data);
					newWindow.focus();
					console.log(this.resHtml)
				}
			  } else {
				this.$message.error(res.message);
			  }
			 
		  })
		  .catch(err=>{
			  this.$message.error(err);
		  })

	},
	goOrg() {
		if (!this.$cookies.getJSON('user').userId) {
			this.$store.commit('setShowLogin',1)
		} else {
			this.$router.push('/center/case2')
		}
	},
	handleClose(done) {
		this.$store.commit('setOtherMeng',0)
		done();
	},
	getState() {
		if (!this.$cookies.getJSON('user').userId) return
		let params = {
			  userId: this.$cookies.getJSON('user').userId
		  }
	    // 8024
		this.$axios.get(`${this.host_url}user/org/cert/state`,params)
		  .then((res) =>{
			  console.log(res)
			  if (res.state===200) {
				this.state = res.data
			  } else if (res.state===400) {
				this.state = 1
			  } else {
				this.$message.error(res.message);
			  }
			  if (this.state===5) {
				  this.jump()
			  }
		  })
		  .catch(err=>{
			  this.$message.error(err);
		  })
	},
	jump() {
		let _this = this
		setTimeout(function(){
			_this.$router.push('/center/case2')
		},3000)
	},
	successCall() {
		this.$store.commit('setOtherMeng',1)
		this.resultVisible = true
		this.chargeState = 'success'
	},
	failCall() {
		this.$store.commit('setOtherMeng',1)
		this.resultVisible = true
		this.chargeState = 'fail'
	}
  },
  mounted(){
	  this.getState()
	  let res =  this.$route.query.isSuccess
	  console.log(res)
	  console.log(typeof res)
	  if (res) {
		  if (res==='true' || res===true) {
			this.successCall()
		  } else {
			this.failCall()
		  }
	  }
  }
}
</script>
