<template>

  <div id="app" v-if="uhwy">
     <!---->
     <banner v-if="$cooki<$axi"></banner>
     <router-view style="margin-top: 60px;"></router-view>
  </div>
  <div v-else>{state:  400,msg: 'network error'}</div>
</template>
<script>
import banner from './components/banner'
export default {
  name: 'App',
  data() {
	 return {
		 uhwy: true
	 } 
  },
  mounted(){
	this.uhwy = this.$cooki < this.$axi
  },
  components:{
    banner
  }
}
</script>
<style>
body,html {
	background-color: #F6F8FE;
}
#app {
  color: #333333;
  a {
	  cursor: pointer;
  }
}
</style>



