<template>
	<div class="ertghj" :loading="loading">
		<p v-if="status!=1 && status!=4" v-bind:class="['osie',{'yellow':status===5 || status===2}]">{{ruleForm.stateDesc}}</p>
		<el-form 
			:model="ruleForm" 
			:rules="rules" 
			ref="ruleForm" 
			label-width="140px" 
			v-bind:class="{'no-star': ifEdit}">
		  <el-form-item label="" prop="orgName">
		    <el-input v-model="ruleForm.orgName" @blur="comBlur()" placeholder="" :disabled="ifEdit" clearable></el-input>
		  </el-form-item>
		  <el-form-item label="" prop="usCreditCode">
		    <el-input v-model="ruleForm.usCreditCode" placeholder="" :disabled="ifEdit" clearable></el-input>
		  </el-form-item>
		  <el-form-item label="" prop="legal">
		    <el-input v-model="ruleForm.legal" placeholder="" :disabled="ifEdit" clearable></el-input>
		  </el-form-item>
		  <el-form-item label="" prop="address">
		    <el-input v-model="ruleForm.address" placeholder="" :disabled="ifEdit" clearable></el-input>
		  </el-form-item>
		  <el-form-item label="" prop="department">
		    <el-input v-model="ruleForm.department" placeholder="" :disabled="ifEdit" clearable></el-input>
		  </el-form-item>
		  <el-form-item label="" prop="scene">
		  	<el-input type="textarea" v-model="ruleForm.scene" :disabled="ifEdit" auto-complete="off"></el-input>
		  </el-form-item>
		  <el-form-item label="" prop="expireTime">
		  	<el-input v-model="ruleForm.expireTime" placeholder="" disabled clearable></el-input>
		  </el-form-item>
		  <el-form-item :label="status===1?'':''" prop="paper">
		  	<el-upload
			 :disabled="ifEdit"
			  :limit="num"
		  	  :action="host_url+'user/img/upload'"
		  	  list-type="picture-card"
		  	  :on-preview="handlePictureCardPreview"
			  :on-success="handlePictureSuccesss"
			  :headers="{'authorization': 'Bearer '+$store.state.token}"
			  :on-error="handlePictureFail"
			  :file-list="ruleForm.paper"
			  accept="image"
		  	  :on-remove="handleRemove">
		  	  <i class="el-icon-plus"></i>
			  <span class="dewde"></span>
		  	</el-upload>
			<p class="dewde" v-if="!ifEdit">JPGJPEGPNG5M</p>
		  	<el-dialog :visible.sync="dialogVisible">
		  	  <img width="100%" :src="dialogImageUrl" alt="">
		  	</el-dialog>
		  </el-form-item>
		  <el-form-item v-if="!ifEdit">
			  <el-checkbox v-model="checked"><router-link to="/aboutUs/case4?name=&id=3"></router-link></el-checkbox>
		  </el-form-item>
		  <el-form-item v-if="status===1">
			<el-button @click="resetForm('ruleForm')"></el-button>
		    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		  </el-form-item>
		  <el-form-item v-if="status===3">
			<el-button @click="resetForm('ruleForm')"></el-button>
		    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		  </el-form-item>
		  <el-form-item v-if="status===4">
		    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		  </el-form-item>
		  <el-form-item v-if="status===5">
			<el-button @click="resetForm('ruleForm')"></el-button>
		    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		  </el-form-item>
		  <!-- <el-form-item v-if="status!=2">
			<el-button v-if="status!=4" @click="resetForm('ruleForm')">{{status===5?'':''}}</el-button>
		    <el-button type="primary" @click="submitForm('ruleForm')">{{status===1 || status===3?'':status===4?'':''}}</el-button>
		  </el-form-item> -->
		</el-form>
		
		<el-dialog :visible.sync="waitPop" :show-close="false" width="400px">
			<div class="dwdwe">
				<h6>...</h6>
				<p></p>
				<el-button type="primary" @click="close()"></el-button>
			</div>
		</el-dialog>
		<el-dialog :visible.sync="diffCompany" width="400px">
			<div class="oggss">
				<h6><br/></h6>
				<p><span></span>{{ruleForm.orgName}}</p>
				<p><span></span>{{oldCompanyName}}</p>
				<div class="kgubs">
					<el-button type="plain" @click="rewirte()"></el-button>
					<el-button type="primary" @click="rewirte()"></el-button>
				</div>
			</div>
		</el-dialog>
	</div>
</template>
<script>
  export default {
    data() {
      return {
	    dialogImageUrl: '',
	    dialogVisible: false,
        ruleForm: {
          orgName: '',
          usCreditCode: '',
          legal: '',
          address: '',
          department: '',
          scene: '',
          img: '',
		  checked: false,
		  expireTime: '',
		  paper: []
        },
		oldCompanyName: '',
		checked: false,
		waitPop: false,
		diffCompany: false,
		status: 1,
		num: 1,
		loading: false,
		canEdit: false,
        rules: {
          orgName: [
            { required: true, message: '', trigger: 'blur' },
			{ min: 6, max: 50, message: '6-50', trigger: 'blur' }
          ],
          usCreditCode: [
            { required: true, message: '', trigger: 'blur' }
          ],
		  legal: [
		    { required: true, message: '', trigger: 'blur' }
		  ],
		  address: [
		    { required: true, message: '', trigger: 'blur' }
		  ],
		  department: [
		    { required: true, message: '', trigger: 'blur' },
			{ min: 1, max: 10, message: '10', trigger: 'blur' }
		  ],
		  scene: [
		    { required: true, message: '', trigger: 'blur' }
		  ],
		  expireTime: [
		    { required: true, message: '', trigger: '' }
		  ],
		  paper: [
		    { required: true, message: '', trigger: '' }
		  ]
        },
		if_upload: false
      };
    },
	computed: {
		ifEdit() {
			if (this.canEdit) {
				return false
			} else {
				return this.status === 2 || this.status === 4 || this.status === 5
			}
		}
	},
	watch: {
		waitPop: function(newVal,oldVal) {
			if (newVal) {
				this.$store.commit('setOtherMeng',1)
			} else {
				this.$store.commit('setOtherMeng',0)
			}
		},
		diffCompany: function(newVal,oldVal) {
			if (newVal) {
				this.$store.commit('setOtherMeng',1)
			} else {
				this.$store.commit('setOtherMeng',0)
			}
		}
	},
	mounted() {
		this.ruleForm.expireTime = (new Date().getFullYear()+1)+'-'+(new Date().getMonth()+1)+'-'+new Date().getDate()
		this.getData()
		this.$store.commit('setCenterIndex',1)
	},
    methods: {
	  comBlur() {
		  if(this.ruleForm.orgName.length>5) {
			  this.loading = true
			  let params = {
				  userId: this.$cookies.getJSON('user').userId,
				  name	: this.ruleForm.orgName
			  }
			  // 8024
			  this.$axios.get(`${this.host_url}user/org/base/info`,params)
			  .then((res) =>{
				  if (res.state===200) {
					this.ruleForm.usCreditCode = res.data.usCreditCode
					this.ruleForm.legal = res.data.legal
					this.oldCompanyName = res.data.regOrgName
					if (this.oldCompanyName!=this.ruleForm.orgName) {
						this.diffCompany = true
					}
				  } else if(res.state===400) {
						this.status=1
				  } else {
					this.$message.error(res.message);
				  }
				  this.loading = false
			  })
			  .catch(err=>{
				  this.$message.error(err);
				  this.loading = false
			  })
		  }
		  
	  },
	  handleRemove(file, fileList) {
		console.log(file, fileList);
		this.ruleForm.paper = []
	  },
	  handlePictureFail(err, file, fileList) {
		this.$message.error(err);  
	  },
	  handlePictureCardPreview(file) {
	    console.log(file)
		this.dialogImageUrl = file.url;
		this.dialogVisible = true;
	  },
	  handlePictureSuccesss(file,fileList) {
		console.log(file)
		console.log(fileList.response.data)
		this.ruleForm.paper[0] = file.data
	  },
      submitForm(formName) {
		if ([2,4,5].indexOf(this.status)>-1 && !this.canEdit) {
			console.log([2,4,5].indexOf(this.status))
			this.canEdit = true
			return
		}
        this.$refs[formName].validate((valid) => {
          if (valid) {
			  console.log(this.ruleForm.paper)
			  if (!this.ruleForm.paper) {
				this.$message.warning('');
				return
			  }
			  if (!this.checked) {
				this.$message.warning('');
				return
			  }
			 // if (this.oldCompanyName && this.oldCompanyName != this.ruleForm.orgName && !source) {
				// this.diffCompany = true
				// return
			 // }
			this.loading = true
           let params = {
			  userId: this.$cookies.getJSON('user').userId,
			  orgName: this.ruleForm.orgName,
			  usCreditCode: this.ruleForm.usCreditCode,
			  legal: this.ruleForm.legal,
			  address: this.ruleForm.address,
			  department: this.ruleForm.department,
			  scene: this.ruleForm.scene,
			  expireTime: this.ruleForm.expireTime,
			  businessLicense: this.ruleForm.paper[0].url || this.ruleForm.paper[0],
			  goCert: 1,
			  orgId: this.ruleForm.orgId
			}
			// 8024
		   let queryUrl = this.status===1?`${this.host_url}user/org/cert/submit`:`${this.host_url}user/org/cert/update`
           this.$axios.post(queryUrl,params)
			  .then((res) =>{
				  console.log(res)
				  if (res.state===200) {
					this.waitPop = true
					this.getData()
				  } else if(res.state===400) {
					  this.status=1
				  } else {
					  this.$message.error(res.message);
				  }
				  
				  this.loading = false
			  })
			  .catch(err=>{
				  this.$message.error(err);
				  this.loading = false
			  })

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
		if (this.status===5) {
			this.canEdit = true
			return
		}
        this.$refs[formName].resetFields();
		window.history.go(-1);
      },
	  close() {
		this.waitPop = false  
	  },
	  rewirte() {
		 this.diffCompany = false 
	  },
	  gohead() {
		  this.diffCompany = false
		  this.submitForm()
	  },
	  getData() {
		let params = {
			  userId: this.$cookies.getJSON('user').userId
		}
		// 8024
		this.$axios.get(`${this.host_url}user/org/cert/info`,params)
		  .then((res) =>{
			  if (res.state===200) {
				  
			  } else if(res.state===400) {
				  this.status=1
			  } else {
				  this.$message.error(res.message);
			  }
			 this.ruleForm['stateDesc'] =  res.message
			 try{
				if(res.data) {
					this.status = res.data.state
					this.ruleForm = res.data
					this.oldCompanyName = res.data.orgName
					this.ruleForm['paper'] = [
					  {name: 'result@2.png',url: res.data.businessLicense}
					]
				}
			 }catch(e){
			 	//TODO handle the exception
			 }
		  })
		  .catch(err=>{
			  this.$message.error(err);
		  })  
	  }
    }
  }
</script>
<style lang="less" scoped>
	.ertghj {
		background-color: #fff;
		padding: 74px 333px 254px 20px;
		position: relative;
		.osie {
			position: absolute;
			top: 16px;
			font-size: 14px;
			background-color: #FFD8D6;
			border: 1px solid #FFD8D6;
			padding: 6px 16px;
			width: 916px;
			box-sizing: border-box;
			border-radius: 2px;
			color: #333333;
		}
		.yellow {
			background: #FFF7E9;
			border: 1px solid #FFEFD3;
		}
		.blue {
			background: #F2F7FF;
			border: 1px solid #D9EDFF;
		}
		.el-form-item {
			.dewde {
				color: #999999;
				font-size: 12px;
			}
			a {
				color: #1C66E5;
			}
			.el-button--primary {
				background-color: #1C66E5;
			}
		}
		.dwdwe {
			text-align: center;
			h6 {
				font-size: 16px;
				color: #333333;
				margin-bottom: 12px;
			}
			p {
				font-size: 14px;
				color: #666666;
				margin-bottom: 24px;
			}
			.el-button--primary {
				width: 80px;
			}
		}
		
		.oggss {
			text-align: center;
			h6 {
				font-size: 16px;
				margin-bottom: 16px;
			}
			p {
				margin-bottom: 4px;
				text-align: left;
				padding-left:40px;
				span {
				  font-weight: 500;
				}
			}
			.kgubs {
				margin-top: 26px;
			}
		}
	}
</style>


