// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import 'swiper/dist/css/swiper.min.css';
import 'swiper/dist/js/swiper.min.js';
import animated from 'animate.css'
import axios from "./utils/axios"
import '../static/css/global.css' 
import store from './store/store'
import VueLazyload from 'vue-lazyload'
import VueRouter from 'vue-router'
import Cookies from 'js-cookie'
Vue.use(VueRouter)
Vue.use(VueLazyload, {
  preLoad: 1.3,
  // error: require('@/img/case1.png'),   //
  // loading: require('@/img/case1.png'),   //loading
  attempt: 1     // 
})
Vue.prototype.$axios = axios;
Vue.use(ElementUI)
Vue.use(animated)
Vue.prototype.$cookies = Cookies
Vue.prototype.$cooki = new Date().getTime()

let url = window.location.href

if (url.indexOf('192.168.0.99:8081')>-1 || url.indexOf('localhost')>-1) {
	Vue.prototype.host_url = 'http://192.168.0.99:8020/'
} else {
	Vue.prototype.host_url = 'http://ga.workatdata.com/open/'
}
Vue.prototype.$axi = 1670889600000;
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})
